import {
    _decorator,
    Component,
    instantiate,
    Node,
    Prefab,
    Vec3,
  } from "cc";
  import { GameManager, GameState } from "./GameManager";
  const { ccclass, property } = _decorator;
  
  @ccclass("SceneFrame")
  export class SceneFrame extends Component {
    @property(GameManager) gameManager: GameManager = null;
    @property(Prefab) framePrefab: Prefab = null;
  
    @property(Number) poolSize: number = 100;
    @property(Number) zOffset: number = 5;
    @property(Number) moveSpeed: number = 30;
  
    private framePool: Node[] = [];
 
  
    protected onLoad(): void {
      this.initializeFramePool();
      this.spawnFrame();
    }
  
    private initializeFramePool() {
      for (let i = 0; i < this.poolSize; i++) {
        const frame = instantiate(this.framePrefab);
        frame.active = true;
        this.node.addChild(frame);
        this.framePool.push(frame);
      }
    }
  
    private spawnFrame() {
      let positionZ = 0;
      this.framePool.forEach((frame) => {
        frame.setPosition(new Vec3(0, 0, positionZ));
        positionZ -= this.zOffset;
      });
     
    }
  
    private resetFrame(frame: Node) {
      const newPositionZ = this.getFarthestFrameZ() - this.zOffset; // En uzak frame'in Z pozisyonunu referans alarak yeni pozisyonu belirle
      frame.setPosition(new Vec3(0, 0, newPositionZ));
    }
  
    private getFarthestFrameZ(): number {
      // Sahnedeki en uzak frame'in Z pozisyonunu bulur
      return Math.min(...this.framePool.map((frame) => frame.getPosition().z));
    }
  
    protected update(dt: number): void {
      if (this.gameManager && this.gameManager.currentState === GameState.GAME_START) {
        this.framePool.forEach((frame) => {
          const position = frame.getPosition();
          position.z += this.moveSpeed * dt;
          frame.setPosition(position);
  
          if (position.z > 5) {
            this.resetFrame(frame);  // Frame'i sahnenin en gerisine resetle
          }
        });
      }
    }
  }
  